body{
  font-family: 'Lato', sans-serif;
}
.login-card{
  align-content: center;
  min-height: 70%;  
  min-height: 70vh; 
  display: flex;
  align-items: center;
 }

.login-holder{
  border-style: solid;
  border-radius: 24px;
  margin: auto;
  border-color: 1px   black;
}
.img-fluid{
  height: 240px;
  width: 480px;
}
.news-title{
  font-weight:bolder;
}
.jumbotron{
  background-color: rgb(66, 66, 66);
}
.jumbotron > h1 {
  color: #fff;
}
.add-news-btn{
  width: 50vh;
  border-radius: 24px;
  
}
.edit-news-cont{
  margin-top: 1vw;
  -webkit-box-shadow: 4px 3px 34px -8px rgba(0,0,0,0.67);
  -moz-box-shadow: 4px 3px 34px -8px rgba(0,0,0,0.67);
  box-shadow: 4px 3px 34px -8px rgba(0,0,0,0.67);
}
.limit{
  word-wrap: break-word;
}
h3{
  font-size: 1vw;
}